<template>
<div class="cardInMony">
     <topNavAct title="会员充值" url="/" :act="actions"></topNavAct>
    <div class="search">
        <div class="searchBox">
            <div class="vipTop">
                <div class="iconfont icon-saoma" @click="scanVip"></div>
                <input class="orderNumer" type="text" placeholder="会员卡号/姓名/手机" v-model="cardInfo">
                <div class="searchResult" @click="searchCard">查询</div>
            </div>
        </div>
    </div>
    <div class="vipList" v-for="(item, index) in info" :key="index" @click="selectCard(item,index)">
        <div class="blueLind"></div>
        <div class="vipCheckBox">
            <div class="greenCheck">
                <div class="checkBox">
                    <div class="iconfont " v-bind:class="{ 'icon-right':  item.isSelect  }"></div>
                </div>
            </div>
        </div>
        <div class="infoL">
            <div class="lineInfo">
                <div class="lineInfo">
                    <div class="titleThird">卡号：</div>
                    <span>{{item.cardNo}}</span>
                </div>
                <div class="titleThird">姓名：</div>
                <span>{{item.cardInfo}}</span>
            </div>
            <div class="lineInfo">
                <div class="titleThird nomalL">手机号：</div>
                <span>{{item.mobile}}</span>
            </div>
        </div>
        <div class="infoL">
            <div class="lineInfo">

                <div class="titleS">余额：</div>
                <span> ￥{{item.balance}}</span>
            </div>
            <div class="lineInfo">

                <div class="titleS">状态：</div>
                <span>{{item.sta}}</span>
            </div>
            <div class="lineInfo">
                <div class="titleThird">优惠：</div>

                <span>{{item.dscMode}}</span>
            </div>
        </div>
    </div>
    <div class="footerChoice" v-if="cardDetail">
        <ul class="payInfo">
            <li>
                <div class="title">会员卡号：</div>
                <div class="v" style="color:red">
                    {{cardDetail.cardNo}}</div>
            </li>
            <li>
                <div class="title">客人姓名：</div>
                <div class="v">
                    {{cardDetail.cardInfo}}</div>
            </li>
            <li>
                <div class="title">联系方式：</div>
                <div class="v">
                    {{cardDetail.mobile}}</div>
            </li>

            <li>
                <div class="title">状态：</div>
                <div class="v">
                    {{cardDetail.sta}}</div>
            </li>

            <li>
                <div class="title">余额：</div>
                <div class="v">
                    ￥{{cardDetail.balance}}</div>
            </li>
            <li class="f">
                <div class="titleS">充值金额：</diV>
                <div class="payFirst">
                    <input type="text" style="width:4.5rem" v-model="money"> </div>
            </li>
        </ul>
        <div class="fixL">
            <ul v-if="SYS_DEVICE == 'NG'">
                <li @click="checkUnionPay(true)" style="width:90%">扫码支付</li>
            </ul>
            <ul v-else>
                <li @click="checkUnionPay(false)">银行卡支付</li>
                <li @click="checkUnionPay(true)">扫码支付</li>
            </ul>
        </div>
    </div>

    <div v-bind:class="{ 'hide': !isEmpty }">
        <img class="nothing" src="../../assets/img/nothing.png">
        <p class="nothingTxt" v-show="!isFrist">什么都没有</p>
        <p class="nothingTxt" v-show="isFrist">请先搜索待充值的会员卡</p>
    </div>
</div>
</template>

<script>
import moment from 'moment'
import {
    Toast,
    MessageBox
} from 'mint-ui';
import md5 from 'js-md5';
import topNavAct from "../common/topNavAct";
export default {
    data() {
        return {
            cardInfo: null,
            info: [],
            isEmpty: true,
            isFrist: true,
            money: null,
            actions: [{
            name: "充值查询",
            method: this.getHis
        }],
            popupVisible: false,
            SYS_DEVICE: "",
            cardDetail: null,
            orderNo: null,
            lastPrintObj:null,
        };
    },
    mounted() {
        /*{
            name: "支付核对",
            method: this.reDoFunc
        },*/
        this.SYS_DEVICE = this.$SYS_DEVICE;
        if (this.$route.query.cardNo) {
            this.cardInfo = this.$route.query.cardNo;
            this.searchCard();
        }
        if (this.orderNo == null) {
            this.getToken(true);
        }

    },
    methods: {
        getHis() {
            if (!this.cardDetail) {
                this.msg('请先搜索会员');
                return;
            }
            this.$router.push('/CardInMoneyHis?cardNo=' + this.cardDetail.cardNo + "&cardId=" + this.cardDetail.cardId + "&redirect=" + encodeURIComponent('/cardInMony?cardNo=' + this.cardDetail.cardNo));
        },
        checkUnionPay(bscan) {
            if (this.money <= 0) {
                this.msg('充值金额不正确');
                return;
            }
            if (parseFloat(this.money) != this.money) {
                this.msg('充值金额不正确');
                return;
            }
            if (!this.orderNo) {
                this.msg('支付订单号不正确，请重新进入');
                return false;
            }
            if (bscan) {
                unionScan(
                    this.successUnionPay,
                    null,
                    Math.round(this.money * 100),
                    this.orderNo
                );
            } else {
                unionPay(
                    this.successUnionPay,
                    null,
                    Math.round(this.money * 100),
                    this.orderNo
                );
            }
        },

        getOrderNo2(t,bnew) {
            var item = localStorage.getItem("inMoneyNo");
            if (bnew || item == null) {
                var gatewayJson = localStorage.getItem("gateway");
                var gateway = null;
                if (gatewayJson != null) {
                    gateway = JSON.parse(gatewayJson);
                    item = gateway.hotelCode + "_" + t;
                } else {
                    item = t;
                }
                localStorage.setItem("inMoneyNo", item);

            }
            this.orderNo = item;
            return item;
        },
        reDoFunc() {
            var item = localStorage.getItem("inMoneyNo");
            if (item == null) {
                this.msg('没有未成功的记录');
                retun;
            }
            reAccount(this.successUnionPay, null, item);
        },
        selectCard(item, index) {
            this.cardDetail = item;
            this.info = [];
        },
        successUnionPay(value) {
            if (value.transData.resCode == "00") {
                this.checkOutOrder(value.transData);
            } else {
                this.msg2(value.transData.resDesc);
            }
        },
        // 扫描
        scanVip() {
            ScanData(this.scanVipSucceed, null);
        },
        scanVipSucceed(value) {
            if (value != null && value != "") {
                if (value.length < 100) {
                    this.$http
                        .post("/pos/posUnion/qrToCardInfo", {
                            paycode: value
                        })
                        .then(res => {
                            if (res.data.errorCode == 0) {
                                this.cardInfo = res.data.result.cardNo;
                                this.searchCard();
                            } else {
                                this.msg(res.data.errorMsg);
                            }
                        });

                } else {
                    this.msg("不是有效会员号");
                }
            } else {
                this.msg("扫描数据为空");
            }
        },
        getToken(bnew) {
            this.$http.post("/pos/posUnion/getCommitToken",{}).then(res => {
                if (res.data.errorCode == 0) {
                    return this.getOrderNo2(res.data.result,bnew);
                } else {
                    this.msg(res.data.errorMsg);
                }
            });
        },
        printData(old,money,cardNo,no){
            var gatewayJson = localStorage.getItem("gateway");
            var name = "";
            if (gatewayJson != null) {
                var gateway = JSON.parse(gatewayJson);
                name = gateway.hotelName;
            }
            var j = {header:[name,"会员卡充值"],datas:["","时间："+moment().format("YYYY-MM-DD HH:mm:ss"),"流水："+no,"line","会员卡号："+cardNo,"充值金额：￥"+money,"可用余额：￥"+(old+money).toFixed(2)]};
            this.lastPrintObj = j;
            printPub(this.printSuccss,this.printError,j);
       },
       printError(v) {
            MessageBox({
                title: "提示",
                message: "打印出错:" + v + "，继续打印?",
                showCancelButton: true,
                closeOnClickModal: false,
                confirmButtonText: "确定"
            }).then(action => {
                if (action == "confirm") {
                    printPub(this.printSuccss, this.printError, this.lastPrintObj);
                }
            });
        },
       printSuccss(v){

       },
        checkOutOrder(transData) {
            var postObj = {};
            postObj.cardId = this.cardDetail.cardId;
            postObj.money = Number(transData.amt);
            postObj.paycode = getPayWayPOS(transData); // 'PUN';

            var ps = this.orderNo.split('_');
            postObj.token = ps[1];
            var oldD = parseFloat(this.cardDetail.balance);
            this.$http.post("/pos/posUnion/memCardPay", postObj).then(res => {
                if (res.data.errorCode == 0) {
                    localStorage.removeItem("inMoneyNo");
                    this.msg2("充值成功");
                    //支付成功后显示对应的页面内容
                    this.money = null;
                    this.cardInfo = this.cardDetail.cardNo;
                    this.orderNo = null;
                    this.printData(oldD,postObj.money,this.cardInfo,transData.refNo);
                    this.cardDetail.balance = (oldD+postObj.money).toFixed(2);
                    this.getToken(true);
                } else {
                    this.getToken(true);
                    this.msg2(res.data.errorMsg);
                }
            });

        },
        searchCard() {
            if (!this.cardInfo) {
                this.msg('查询内容不能为空');
                return;
            }
            this.$http.post("/pos/posUnion/getCardInfo", {
                cardInfo: this.cardInfo
            }).then(res => {
                if (res.data.errorCode == 0) {
                    if (res.data.result.length == 1) {
                        this.cardDetail = res.data.result[0];
                        this.info = [];
                    } else {
                        this.info = res.data.result;
                        this.cardDetail = null;
                    }
                    this.isEmpty = false;
                    this.isFrist = false;
                } else {
                    this.info = [];
                    this.cardDetail = null;
                    this.isEmpty = true;
                    this.isFrist = false;
                    this.msg(res.data.errorMsg);
                }
            })
        },
        msg2(msg) {
            if (msg == "用户取消") {
                this.msg(msg);
            } else {
                MessageBox.alert(msg);
            }
        },
        msg(msg) {
            Toast({
                message: msg,
                position: 'bottom',
                duration: 3000
            });
        },

    },
    filters: {
        tofixed(v) {
            return parseFloat(v).toFixed(2);
        },

        fd(value) {
            if (value == null || value == "") return "";
            return value.substr(11);
        }
    },
    components: {
        topNavAct
    }
}
</script>

<style lang="less">
.cardInMony {
    .search {
        width: 100%;
        border-radius: 0.2667rem;
        background: #F4F5F7;
        margin: 0 auto 0 auto;
        height: 1.6rem;

        .searchBox {
            height: 0.8rem;
            margin: 0 auto;

            .vipTop {
                width: 96%;
                margin: 0.4rem auto;
                height: 0.8rem;

                .icon-saoma {
                    display: inline-block;
                    font-size: 0.64rem;
                    line-height: 0.8rem;
                    vertical-align: top;
                    margin-right: 0.3467rem;
                    float: left;
                }

                .orderNumer {
                    display: inline-block;
                    width: 62% !important;
                    height: 0.8rem;
                    font-size: 0.3733rem;
                    padding-left: 0.2667rem;
                    background: #fff;
                    border: 0.0267rem solid #cccccc;
                    border-radius: 0.1333rem;
                    float: left;
                }

                .searchResult {
                    display: inline-block;
                    width: 20.5%;
                    height: 0.8rem;
                    font-size: 0.3733rem;
                    background: #d8af68;
                    border-radius: 0.1867rem;
                    float: right;
                    color: #fff;
                    line-height: 0.7467rem;
                }
            }
        }
    }

    .vipList {
        background: #ffffff;
        margin-bottom: 0.2667rem;
        width: 96%;
        height: 2.5067rem;
        margin: 0 auto 0.2667rem;
        box-shadow: 0 0 0.2133rem 0rem #dadada;
        border-radius: 0.1333rem;
        position: relative;
        padding-top: 0.3rem;

        .blueLind {
            height: 0.3733rem;
            width: 1.1%;
            background: #646f83;
            display: inline-block;
            float: left;
            margin-top: 0.4rem;
        }

        .lrp {
            width: 96%;
            float: left;
            margin-left: 0.2rem;

            li {
                width: 47%;
                float: left;
                text-align: left;
                padding: 0.2rem 0 0 0.2rem;

                label {
                    color: #aaa;
                }
            }

            .p100 {
                width: 100%;
            }
        }

        .infoL {
            width: 46%;
            height: auto;
            text-align: left;
            display: inline-block;
            padding-top: 0.1067rem;
            margin-left: 0.2667rem;

            .lineInfo {
                margin-bottom: 0.1067rem;
                color: #999999;

                .titleThird {
                    display: inline-block;
                    color: #999999;
                }

                .titleS {
                    display: inline-block;
                    color: #999999;
                }

                .nomalL {
                    letter-spacing: 0rem;
                }

                span {
                    font-size: 0.3733rem;
                    color: #151515;
                }
            }
        }

        .vipCheckBox {

            top: 1rem;
            right: 0.2rem;
            position: absolute;

            .greenCheck {

                .checkBox {
                    width: 0.4267rem;
                    height: 0.4267rem;
                    margin: 0 auto;
                    border: 0.0267rem solid #cccccc;
                    border-radius: 0.1333rem;

                    .icon-right {
                        color: #6ed200;
                        line-height: 0.4267rem;
                    }
                }
            }
        }
    }

    .payInfo {
        width: 90%;
        margin: 0 auto;
        position: relative;
        box-shadow: 0 -2px 20px rgba(0, 0, 0, 0.15);
        border-radius: 4px;
        margin-bottom: 0.6rem;
        padding-top: 0.2rem;
        background: #fff;
        margin-top:.5rem;

        li {
            width: 90%;
            height: 1rem;
            line-height: 1rem;
            clear: both;
            margin: 0 auto;

            .bk {
                width: 0.15rem;
                height: 0.4rem;
                background-color: #f6a624;
                position: absolute;
                top: 0.5rem;
                left: 0.1rem;
                border-radius: 0.07rem;
            }

            &.f {
                height: 2.5rem;
                margin-top: 0.5rem;
                border-top: 1px dashed #666;
                line-height: 2.5rem;
            }

            .title {
                width: 50%;
                text-align: left;
                font-size: 0.35rem;
                color: #999999;
                float: left;
            }

            .v {
                text-align: right;
                font-size: 0.4rem;
                float: right;
                width: 50%;
            }

            .titleS {
                width: 35%;
                text-align: left;
                font-size: 0.5rem;
                color: #999999;
                float: left;
            }

            .payFirst {
                text-align: right;
                font-size: 0.7467rem;
                color: red;
                float: right;
            }
        }
    }

    .footerChoice {
        width: 100%;
        height: auto;

        font-size: 0.3733rem;

        .fixL {
            width: 78.7%;
            display: inline-block;

            ul {
                margin-top: 0.4rem;
                margin-bottom: 0.4rem;

                li {
                    width: 47.2%;
                    height: 1.3rem;
                    background: #ff8902;
                    border-radius: 0.1867rem;
                    text-align: center;
                    line-height: 1.3rem;
                    display: inline-block;
                    margin-right: 0.1333rem;
                    color: #fff;
                }
            }
        }

        .fixR {
            width: 11.2%;
            display: inline-block;
            vertical-align: top;
            margin-top: 0.4267rem;

            .omit {
                width: 100%;
                height: 0.9867rem;
                line-height: 0.7rem;
                vertical-align: middle;
                background: #ff8902;
                border-radius: 0.1867rem;
                display: inline-block;

                .icon-xia {
                    font-size: 0.56rem;
                    line-height: 0.96rem;
                }
            }
        }
    }

    .nothing {
        width: 80px;
        height: 88px;
        margin: 150px auto 0 auto;
    }

    .nothingTxt {
        text-align: center;
        font-size: 20px;
        color: #CCD1D5;
        margin-top: 20px;
    }

    .show {
        display: block
    }

    .hide {
        display: none;
    }
}
</style>



